.helloMessage {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 50px 0 100px;
}

.helloMessage p {
    max-width: 600px;
    font-size: 30px;
    text-align: center;
}

.helloMessage img {
    width: 250px;
}

@media screen and (max-width: 426px){
    .helloMessage {
        flex-direction: column-reverse;
        margin: 100px 0 20px;
    }
}