.header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.searchForm {
    display: flex;
    flex-direction: row;
    margin-bottom: 15px;
    align-items: center;
    justify-content: space-between;
    /*background-color: #ff8162;*/
    flex-wrap: wrap;
    width: 70%;
    min-width: 669px;
}

@media screen and (max-width: 1025px) {
    .searchForm {
        width: 92%;
        min-width: 300px;
    }

    .inputBtnWrapper {
        width: 35%;
        /*min-width: 322px;*/
    }
}
@media screen and (max-width: 789px) {
    .searchForm {
justify-content: center;
    }
    .inputBtnWrapper {
        width: 35%;
        min-width: 321px;
    }
}


.inputBtnWrapper {
    /*background-color: #7cb0f6;*/
    width: 46%;
    margin: 5px;
}


input {
    display: inline-block;
    outline: none;
    background-color: #0d1117;
    border: 1px solid #21262d;
    border-radius: 6px;
    /*max-width: 400px;*/
    padding: 10px 20px;
    font-size: 20px;
    /*margin-right: 20px;*/
    width: 100%;
}

input:focus {
    border: 1px solid #1f6feb;
}

header button {
    border-radius: 6px;
    /*max-width: 400px;*/
    outline: none;
    border: none;
    padding: 10px 20px;
    font-size: 20px;
    background-color: #1f6feb;
    font-weight: bold;
    cursor: pointer;
    width: 100%;
}

h1 {
    margin: 100px 0 20px;
    text-align: center;
}

@media screen and (max-width: 426px){
    h1 {
        margin: 20px 0 0;
    }
    .searchForm {
        margin-bottom: 100px;
    }
}